@font-face {
  font-family: 'PT_Sans';
  src: local('PT_Sans'), url(./assets/fonts/PT_Sans/PT_Sans-Web-Regular.ttf);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  width: 100%;
  min-height: 100vh;
  background: #100E19 fixed;
  background-size: cover;
  font-family: 'PT_Sans';
  font-size: 16px;
}

.container {
  width: 65%;
  min-height: 100vh;
  margin: 0 auto;
  padding: 0 20px;
  background-color: rgb(52, 49, 73);
}

.authContainer {
  margin: 0 auto;
  padding: 0px;
  width: 80%;
  min-height: 100%;
}

a {
  text-decoration: none;
  color: white;
}
