.loginForm {
    margin: 0;
    width: 360px;
    height: 395px;
    background: #1F1B2E;
    border-radius: 20px;
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    box-sizing: border-box;
}
.containerSign {
    flex-direction: row;
    display: flex;
    text-align: center;
    justify-content: center;
    margin-bottom:5px;

}
.noneActivePageSign {

    font-family: 'Montserrat',sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;

    color: rgba(255, 255, 255, 0.8);

    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.activePageSign {
    font-family: 'Montserrat',sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;

    color: rgba(236, 23, 79, 0.8);

    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.title {
    margin-top: 15px;
    width: 169px;
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    color: #645BA3;
}

.sign {
    width: 132px;
    margin-top: 3px;
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    color: rgba(255, 255, 255, 0.2);
}

.input {
    width: 320px;
    height: 40px;
    margin-top: 17px;
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    border-radius: 7px;
    text-align: center;
    background-color: #645BA3 !important;

}

.input::placeholder {
    color: white;
    opacity: 40%;
}
.input:-webkit-autofill,
.input:-webkit-autofill:hover,
.input:-webkit-autofill:active,
.input:-webkit-autofill:focus {
    background-color: #645BA3 !important;
    color:  #645BA3;
    -webkit-box-shadow: 0 0 0 1000px  #645BA3 inset !important;
    -webkit-text-fill-color: #ffffff !important;
}

.inputTwentyEight {
    margin-top: 28px !important;
}

.errorText {
    margin-top:3px;
    font-family: 'Montserrat',sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 15px;
    color: #EA003D;
}

.isError {
    margin-bottom: 12px;
}
