.loginForm {
    display: flex;
    justify-content: start;
    align-items: center;
    flex-direction: column;
    margin: 0 auto;
    width: 360px;
    height: 330px;
    max-height: 330px;
    background: #1F1B2E;
    border-radius: 20px;
}

.successBtn {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: #FFFFFF;
    width: 100px;
    height: 40px;
    margin-top: 26px;
    background: rgba(56, 165, 80, 0.8);
    border-radius: 5px;




    text-shadow: 2px 4px 4px rgba(0, 0, 0, 0.25);
}

.inputs {
    width: 320px;
    height: 40px;
    margin-top: 34px;
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    background: #645BA3;
    border-radius: 7px;
    text-align: center;
}

.inputs::placeholder{
    color:white !important;
    opacity:40%;
}
.inputs:-webkit-autofill,
.inputs:-webkit-autofill:hover,
.inputs:-webkit-autofill:active,
.inputs:-webkit-autofill:focus {
    background-color: #645BA3 !important;
    color:  #645BA3;
    -webkit-box-shadow: 0 0 0 1000px  #645BA3 inset !important;
    -webkit-text-fill-color: #ffffff !important;
}

.containerSign {
    flex-direction: row;
    display: flex;
    text-align: center;
    justify-content: center;
    margin-bottom:5px;

}

.errorText {
    color: #EA003D;
    margin-top:3px;
    font-family: 'Montserrat',sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
}


.btnGroup {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5px;
    padding-left: 20px;
    padding-right: 20px;
}

.title {
    margin-top: 19px;
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    color: #645BA3;
}

.sign {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 12px;
    margin-top: 3px;
    color: rgba(255, 255, 255, 0.2);
}
.noneActivePageSign {

    font-family: 'Montserrat',sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;

    color: rgba(255, 255, 255, 0.8);

    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.activePageSign {
    font-family: 'Montserrat',sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;

    color: rgba(236, 23, 79, 0.8);

    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.pError {
    width: 20px;
}

.cont {
    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 400px;
    width: 1000px;
}

.buttonSwitch {
    display: flex;
    justify-content: center;
    align-items: center;
}

.forgotPassword {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    color: #ECC153;

}

.container {
    width: 326px;
    display: flex;
    justify-content: start;
    align-items: start;
}

.buttonGroup {
    display: flex;
    justify-content: center;
    align-items: center;
}