.container {
  height: 100vh;
  display: flex;
  justify-content: center;
  padding-top: 60px;
}

.profile {
  display: flex;
  width: 90%;
  height: 80vh;
  color: white;
  border-radius: 20px;
  background-color: rgba(35, 32, 47, 0.7);
}

.side {
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
  border-radius: 20px 0 0 20px;
  background-color: #3b3b25;
}

.title {
  margin-bottom: 40px;
  font-size: 24px;
}

.image {
  margin-bottom: 40px;
}

.name {
  font-size: 30px;
}

.status {
  color: #02d302;
}

.main {
  padding: 20px;
  flex: 3;
  border-radius: 20px;
}

.main > div {
  display: grid;
  grid-template-columns: repeat(2, 50%);
  row-gap: 40px;
  justify-content: center;
}
