.background {
    width: 360px;
    height: 180px;
    display: flex;
    text-align: center;
    flex-direction: column;
    align-items: center;
    background: #1F1B2E;
    border-radius: 20px;
}
.identifyCode {
    margin-top:24px;
    font-family: 'Montserrat',sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    color: #645BA3;
}

.sign{
    font-family: 'Montserrat',sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 12px;
    margin-top:7px;
    color: rgba(255, 255, 255, 0.2);
}
.inputCode {
    width: 140px;
    height: 50px;
    margin-top:18px;
    text-align: center;
    font-family: 'Montserrat',sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    padding:15px 15px 15px 15px;
    background: #645BA3;
    border-radius: 7px;
}
.inputCode::-webkit-outer-spin-button,
.inputCode::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* Firefox */
.inputCode[type=number] {
    -moz-appearance: textfield;
}
.inputCode::placeholder{
    color:white !important;
    opacity:40%;
}

.inputCode:-webkit-autofill,
.inputCode:-webkit-autofill:hover,
.inputCode:-webkit-autofill:active,
.inputCode:-webkit-autofill:focus {
    background-color: #645BA3 !important;
    color:  #645BA3;
    -webkit-box-shadow: 0 0 0 1000px  #645BA3 inset !important;
    -webkit-text-fill-color: #ffffff !important;
}

.errorText {
    align-self: center;
    color: #EA003D;
    margin-top:3px;
    font-family: 'Montserrat',sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
}