.background {
    width: 360px;
    height: 309px;
    left: 1505px;
    top: 122px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background: #1F1B2E;
    border-radius: 20px;
}
.resetPassword {
    width: 181px;
    height: 29px;
    left: 1595px;
    top: 146px;
    font-family: 'Montserrat',sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    margin-top:24px;
    color: #645BA3;
}
.inputPassword {
    text-align: center;
    width: 320px;
    height: 40px;
    left: 1525px;
    top: 216px;
    font-family: 'Montserrat',sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    margin-top:24px;
    background: #645BA3;
    border-radius: 7px;
}
.inputPassword::placeholder{
    color:white !important;
    opacity:40%;
}
.inputPassword:-webkit-autofill,
.inputPassword:-webkit-autofill:hover,
.inputPassword:-webkit-autofill:active,
.inputPassword:-webkit-autofill:focus {
    background-color: #645BA3 !important;
    color:  #645BA3;
    -webkit-box-shadow: 0 0 0 1000px  #645BA3 inset !important;
    -webkit-text-fill-color: #ffffff !important;
}


.sign {
    font-family: 'Montserrat',sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 12px;
    margin-top:5px;
    color: rgba(255, 255, 255, 0.2);
}
.errorText {
    align-self: center;
    color: #EA003D;
    margin-top:3px;
    font-family: 'Montserrat',sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
}