.header {
  position: sticky;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 20px;
  width: 100%;
  height: 70px;
  color: white;
  background-color: #333;
}

ul {
  display: flex;
  align-items: center;
  min-width: 800px;
}

li {
  width: 8vw;
  min-width: 8rem;
  height: 50px;
  padding: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  border: 1px solid #333;
  background-color: #444;
  transform: skew(-20deg);
  list-style-type: none;
}

.element {
  transform: skew(20deg);
}

.firstEl {
  width: 14vw;
  position: relative;
  right: -8px;
  transform: none;
  clip-path: polygon(8% 100%, 92% 100%, 100% 0, 0 0);
}

.firstEl > a {
  transform: none;
}

.link {
  text-decoration: none;
}

.image {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 30px;
}

.image > img {
  position: relative;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.image > img:hover {
  cursor: pointer;
}

.popup {
  position: absolute;
  top: 65px;
  padding: 5px;
  min-width: 200px;
  display: none;
  flex-direction: column;
  font-size: 1.2rem;
  border: 1px solid gray;
  border-radius: 8px;
  background-color: rgb(75, 73, 108);
  z-index: 10;
}

.popup > a {
  transform: none;
}

.popup-active {
  display: flex;
}

.popup * {
  margin: 10px 40px 10px 15px;
}

.popup__btnOut {
  width: 140px;
  height: 40px;
}
