.background {
    width: 360px;
    height: 236px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background: #1F1B2E;
    border-radius: 20px;

}
.confirmationTitle {
    width: 303px;
    height: 29px;
    margin-top:24px;
    font-family: 'Montserrat',sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;

    color: #645BA3;
}

.underTitle {
    font-family: 'Montserrat',sans-serif;
    width: 300px;
    height: 12px;
    left: 100px;
    top: 65px;
    margin-top:3px;
    font-weight: 700;
    font-size: 12px;
    line-height: 12px;
    color: rgba(255, 255, 255, 0.2);
}
.input {
    width: 320px;
    height: 40px;
    font-family: 'Montserrat',sans-serif;
    margin-top:26px;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    text-align: center;
    background: #645BA3;
    border-radius: 7px;
}
.input:-webkit-autofill,
.input:-webkit-autofill:hover,
.input:-webkit-autofill:active,
.input:-webkit-autofill:focus {
    background-color: #645BA3 !important;
    color:  #645BA3;
    -webkit-box-shadow: 0 0 0 1000px  #645BA3 inset !important;
    -webkit-text-fill-color: #ffffff !important;
}

.input::placeholder {
    color:white !important;
    opacity: 40%;
}
.button {
    width: 200px;
    height: 40px;
    left: 1160px;
    top: 284px;
    font-family: 'Montserrat',sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    margin-block: 20px;
    color: #FFFFFF;

    text-shadow: 2px 4px 4px rgba(0, 0, 0, 0.25);
    background: rgba(116, 47, 133, 0.8);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 5px;
}

.errorText {
    margin-top:3px;
    font-family: 'Montserrat',sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 15px;
    color: #EA003D;
}


/* identical to box height */


