
.label {
    width: 300px;
    height: 700px;
    position: left;
    object-fit: cover;
    object-position: 50%;
    border-radius: 6px;
    border: 6px solid rgb(75, 69, 122);
  }

  .btnHidden {
    display: none;
  }

.avatar{
  width: 100px;
    height: 100px;
    position: right;
    object-position: 50%;
    border-radius: 6px;
    border: 6px solid rgb(75, 69, 122);
}