.image {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

@keyframes gradientAnimation {
  0% {
    background-position: 50% 5%;
  }
  25% {
    background-position: 5% 50%;
  }
  50% {
    background-position: 50% 95%;
  }
  75% {
    background-position: 95% 50%;
  }
  100% {
    background-position: 50% 5%;
  }
}

.image:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: radial-gradient(
    circle,
    rgba(218, 0, 255, 1) 25%,
    rgba(238, 55, 5, 1) 65%
  );
  background-size: 300% 300%;
  animation: gradientAnimation 4s ease infinite;
  clip-path: polygon(50% 0%, 90% 25%, 90% 75%, 50% 100%, 10% 75%, 10% 25%);
}

.image > img {
  position: relative;
  width: 150px;
  height: 150px;
  object-fit: cover;
  clip-path: polygon(50% 4%, 86% 27%, 86% 74%, 50% 96%, 14% 74%, 14% 27%);
}

.image > img:hover {
  cursor: pointer;
}
