.button {
  width: 10vw;
  height: 45px;
  background-color: #ececec;
  border: 1px solid #202020;
  border-radius: 4px;
  box-shadow: rgba(0, 0, 0, 0.1) 0 2px 4px 0;
  color: black;
  font-size: 1.2rem;
  padding: 5px 10px;
  text-align: center;
}

@keyframes onHoverAnimation {
  0% {
    background: radial-gradient(
      ellipse at 50% 50%,
      rgb(102, 62, 145),
      rgb(19, 12, 27)
    );
    text-shadow: 1px 1px 2px black, 0 0 0.3em rgb(0, 0, 0);
  }
  20% {
    background: radial-gradient(
      ellipse at 50% 50%,
      rgb(108, 66, 180),
      rgb(19, 12, 27)
    );
  }
  40% {
    background: radial-gradient(
      ellipse at 50% 50%,
      rgb(114, 68, 180),
      rgb(19, 12, 27)
    );
  }
  60% {
    background: radial-gradient(
      ellipse at 50% 50%,
      rgb(120, 70, 180),
      rgb(19, 12, 27)
    );
  }
  80% {
    background: radial-gradient(
      ellipse at 50% 50%,
      rgb(126, 72, 180),
      rgb(19, 12, 27)
    );
  }
  100% {
    background: radial-gradient(
      ellipse at 50% 50%,
      rgb(132, 80, 180),
      rgb(19, 12, 27)
    );
    text-shadow: 1px 1px 2px black, 0 0 0.3em rgb(255, 255, 255);
  }
}

.button:hover {
  box-shadow: rgba(0, 0, 0, 0.15) 0 3px 9px 0;
  cursor: pointer;
}

.success {
  margin-top:25px;
  width: 250px;
  height: 40px;
  font-family: 'Montserrat',sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  color: #FFFFFF;
  background: rgba(116, 47, 133, 0.8);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
}
.success:hover {
  background: rgba(116, 47, 133, 0.8);
  box-shadow: 7px 7px 32px #100D1D, -7px -7px 32px rgba(16, 13, 29, 0.1), inset -4px -4px 10px rgba(0, 0, 0, 0.25), inset 4px 4px 10px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  font-family: 'Montserrat',sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  color: #FFFFFF;
  text-shadow: -1px -1px 16px rgba(255, 255, 255, 0.35), 1px 1px 16px rgba(255, 255, 255, 0.35);
}
.success:active {
  font-family: 'Montserrat',sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  background: rgba(116, 47, 133, 0.8);
  box-shadow: inset 10px 16px 32px #100D1D, inset -10px -14px 32px #100D1D;
  border-radius: 5px;
}
