.profile {
  width: 300px;
  justify-self: center;
}

.title {
  font-size: 20px;
  margin-bottom: 10px;
}

.image {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image > img {
  position: relative;
  width: 400px;
  height: 180px;
  border: 1px solid orange;
  border-radius: 20px;
}

.image > img:hover {
  cursor: pointer;
}
