/*Pd - Personal Data*/
.backgroundPd {
    flex-wrap: wrap;
    height: 202px;
    top: 514px;
    align-items: center;
    background: #1F1B2E;
    border-radius: 5px;
    padding: 20px 30px 100px 20px;
}
.insideBC {
    width: 100%;
    flex-wrap: wrap;

}
.titlePd{
    text-align: center;
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    color: #3EB7B7;
}
.signPd{
    font-style: normal;

    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #8A8A8A;

}
.buttonEdit {


    font-weight: 700;
    font-size: 14px;
    line-height: 17px;

    color: #FFFFFF;
    background: #45A336;
    border-radius: 5px;
}
.buttonActiveEdit {
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;

    color: #FFFFFF;
    background: dodgerblue;
    border-radius: 5px;
}
.inputForSignPd{
    width: auto;
    display: flex;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    color: #EC174F;
}
.columnWithButtons  {
    display: flex;
    justify-content: end;
    justify-items: end;
}