.card {
  color: rgb(255, 255, 255);
  background-color: rgb(16, 14, 25);
  border-radius: 8px;
  padding: 20px;
}

.shell {
  display: flex;
  flex-direction: row;
}

.shell2 {
  display: flex;
  flex-direction: column;
}

.name {
  width: 100%;
  height: 40px;
  text-align: center;
  font-size: 2rem;
  font-weight: 600;
}

.info {
  display: flex;
  flex-direction: column;
  font-size: 17px;
}

.info2 {
  min-width: 300px;
  max-width: 100%;
  min-height: 50px;
  max-height: 380px;
  width: 100%;
  color: white;
  background-color: rgb(31, 27, 46);
  font-size: 17px;
  resize: none;
  border: none;
}

.info2::-webkit-scrollbar {
  height: 0;
}

.textBlock {
  background-color: rgb(31, 27, 46);
  padding: 20px;
  border-radius: 6px;
  margin-left: 10px;
  margin-bottom: 10px;
  width: 50vw;
  display: flex;
  flex-direction: column;
}

.interactive {
  display: flex;
  justify-content: space-between;
  height: 20px;
  padding-top: 10px;
  padding-bottom: 20px;
}

.text {
  height: 95%;
  width: 100%;
}

.text > hr {
  margin-bottom: 10px;
}

.text > p {
  margin-bottom: 10px;
}

.btnGroup {
  display: flex;
  justify-content: flex-end;
  height: 50px;
  bottom: 0;
}

.descriptionElement {
  display: flex;
  align-items: baseline;
}

.descriptionElement > p {
  margin-right: 5px;
}

.btnAdd {
  width: 200px;
}

.btnFavourite {
  width: 300px;
  display: flex;
  justify-content: center;
}

.btnInteractive {
  background-color: gray;
}

.isActive {
  background: red;
}

.isActive:hover {
  background: rgb(184, 66, 66);
}
